import React, { useEffect, useMemo, useState } from 'react';
import { resolveImageUrl } from '../utils/images';

type ImageItem = {
  src: string;
  alt?: string;
};

interface HeroCarouselProps {
  images?: ImageItem[];
  intervalMs?: number;
}

const HeroCarousel: React.FC<HeroCarouselProps> = ({ images, intervalMs = 5000 }) => {
  const defaultImages: ImageItem[] = useMemo(() => {
    const envImage = process.env.REACT_APP_HERO_IMAGE_URL;
    const base: ImageItem[] = [];
    if (envImage && envImage.trim().length > 0) {
      base.push({ src: envImage, alt: 'Nexus Auctions hero' });
    } else {
      base.push({ src: '/assets/nexus-logo.png', alt: 'Nexus Auctions hero' });
    }
    // Backend-served placeholders to avoid ORB/CORS issues
    base.push(
      { src: '/api/placeholder/1920/800?text=Auction+Gavel', alt: 'Auction gavel' },
      { src: '/api/placeholder/1920/800?text=Art+Auction', alt: 'Art auction' },
      { src: '/api/placeholder/1920/800?text=Antique+Marketplace', alt: 'Antique marketplace' }
    );
    return base;
  }, []);

  const slides = images && images.length > 0 ? images : defaultImages;
  const [index, setIndex] = useState(0);
  const [paused, setPaused] = useState(false);

  useEffect(() => {
    if (paused) return;
    const id = setInterval(() => {
      setIndex((i) => (i + 1) % slides.length);
    }, intervalMs);
    return () => clearInterval(id);
  }, [paused, intervalMs, slides.length]);

  const goPrev = () => setIndex((i) => (i - 1 + slides.length) % slides.length);
  const goNext = () => setIndex((i) => (i + 1) % slides.length);

  return (
    <div
      className="absolute inset-0 overflow-hidden"
      onMouseEnter={() => setPaused(true)}
      onMouseLeave={() => setPaused(false)}
    >
      {slides.map((img, i) => (
        <img
          key={img.src + i}
          src={resolveImageUrl(img.src)}
          alt={img.alt || 'Auction banner'}
          referrerPolicy="no-referrer"
          className={`absolute inset-0 w-full h-full object-cover object-center transition-opacity duration-700 ${i === index ? 'opacity-100' : 'opacity-0'}`}
          onError={(e) => { (e.currentTarget as HTMLImageElement).src = '/assets/nexus-logo.png'; }}
        />
      ))}

      {/* Controls */}
      <button
        type="button"
        aria-label="Previous slide"
        onClick={goPrev}
        className="absolute left-4 top-1/2 -translate-y-1/2 bg-black/40 hover:bg-black/60 text-white rounded-full p-2"
      >
        
      </button>
      <button
        type="button"
        aria-label="Next slide"
        onClick={goNext}
        className="absolute right-4 top-1/2 -translate-y-1/2 bg-black/40 hover:bg-black/60 text-white rounded-full p-2"
      >
        
      </button>

      {/* Indicators */}
      <div className="absolute bottom-6 left-1/2 -translate-x-1/2 flex gap-2">
        {slides.map((_, i) => (
          <button
            key={`dot-${i}`}
            aria-label={`Go to slide ${i + 1}`}
            onClick={() => setIndex(i)}
            className={`h-2 w-2 rounded-full ${i === index ? 'bg-white' : 'bg-white/50'} hover:bg-white`}
          />
        ))}
      </div>
    </div>
  );
};

export default HeroCarousel;