import React from 'react';
import { apiEndpoints } from '../services/api';

export default function PayableCalculator({ auction }: { auction: any }) {
  const [amount, setAmount] = React.useState<string>('');
  const [result, setResult] = React.useState<any | null>(null);
  const [loading, setLoading] = React.useState<boolean>(false);
  const [error, setError] = React.useState<string | null>(null);

  const onCalculate = async () => {
    try {
      setLoading(true);
      setError(null);
      const val = parseFloat(amount);
      if (!isFinite(val) || val <= 0) {
        setError('Enter a valid amount');
        setLoading(false);
        return;
      }
      const { data } = await apiEndpoints.bids.getPayable(auction._id, val);
      setResult(data?.data || null);
    } catch (e: any) {
      setError(e?.response?.data?.message || 'Failed to compute');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="mt-6 border border-gray-200 rounded-lg p-4 bg-white">
      <div className="flex items-center justify-between mb-3">
        <h4 className="text-lg font-semibold text-gray-900">Total Payable Calculator</h4>
      </div>
      <div className="flex gap-3 mb-3">
        <input
          type="number"
          value={amount}
          onChange={(e) => setAmount(e.target.value)}
          placeholder="Enter bid amount"
          className="flex-1 px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
        <button
          onClick={onCalculate}
          disabled={loading}
          className={`px-4 py-2 rounded-md text-sm font-semibold ${loading ? 'bg-gray-200 text-gray-700 cursor-not-allowed' : 'bg-black text-white hover:bg-gray-900'}`}
        >
          {loading ? 'Calculating' : 'Calculate'}
        </button>
      </div>
      {error && <div className="text-sm text-red-700 bg-red-50 border border-red-200 rounded px-3 py-2 mb-3">{error}</div>}
      {result && (
        <div className="text-sm text-gray-800">
          <div className="flex justify-between"><span>Bid</span><span>${Number(result.bid).toFixed(2)}</span></div>
          {Number(result.commission) > 0 && (
            <div className="flex justify-between"><span>Buyers Commission ({(Number(result.commissionRate) * 100).toFixed(2)}%)</span><span>${Number(result.commission).toFixed(2)}</span></div>
          )}
          {Number(result.vatOnBid) > 0 && (
            <div className="flex justify-between"><span>VAT on Bid ({(Number(result.vatRate) * 100).toFixed(2)}%)</span><span>${Number(result.vatOnBid).toFixed(2)}</span></div>
          )}
          {Number(result.vatOnCommission) > 0 && (
            <div className="flex justify-between"><span>VAT on Commission ({(Number(result.vatRate) * 100).toFixed(2)}%)</span><span>${Number(result.vatOnCommission).toFixed(2)}</span></div>
          )}
          {Number(result.stc) > 0 && (
            <div className="flex justify-between"><span>STC ({(Number(result.stcRate) * 100).toFixed(2)}%)</span><span>${Number(result.stc).toFixed(2)}</span></div>
          )}
          <div className="flex justify-between font-semibold border-t pt-2 mt-2"><span>Total</span><span>${Number(result.total).toFixed(2)}</span></div>
        </div>
      )}
    </div>
  );
}