import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { apiEndpoints } from '../services/api';
import RegistrationBadge from '../components/RegistrationBadge';

interface RegItem {
  _id: string;
  userId: { username?: string; email?: string; firstName?: string; lastName?: string; _id?: string } | string;
  status: string;
  depositAmount?: number;
  depositRef?: string;
  depositReceiptUrl?: string;
  depositVerification?: { status?: string; notes?: string; verifiedBy?: any; verifiedAt?: string };
  kycStatus?: string;
  createdAt?: string;
  updatedAt?: string;
}

export default function AdminRegistrations() {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [items, setItems] = useState<RegItem[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string>('');
  const [statusFilter, setStatusFilter] = useState<string>('');
  const [q, setQ] = useState<string>('');
  const [modal, setModal] = useState<{ type: 'status' | 'deposit' | 'verify'; regId: string } | null>(null);
  const [statusUpdate, setStatusUpdate] = useState<{ status: string; notes?: string }>({ status: 'approved' });
  const [depositUpdate, setDepositUpdate] = useState<{ depositAmount?: number; depositRef?: string; status?: string }>({});
  const [verifyUpdate, setVerifyUpdate] = useState<{ verificationStatus: 'reviewing' | 'verified' | 'rejected'; notes?: string; approve?: boolean }>({ verificationStatus: 'reviewing' });

  const load = async () => {
    if (!id) return;
    try {
      setLoading(true);
      const { data: res } = await apiEndpoints.auctions.admin.listRegistrations(id, { status: statusFilter, q });
      const rows: RegItem[] = res?.data || res || [];
      setItems(rows);
    } catch (e: any) {
      setError(e?.message || 'Failed to load registrations');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => { load(); /* eslint-disable-next-line */ }, [id, statusFilter, q]);

  const openStatusModal = (regId: string, current: string) => {
    setStatusUpdate({ status: current });
    setModal({ type: 'status', regId });
  };

  const openDepositModal = (regId: string, currentAmount?: number, currentRef?: string) => {
    setDepositUpdate({ depositAmount: currentAmount, depositRef: currentRef });
    setModal({ type: 'deposit', regId });
  };
  const openVerifyModal = (regId: string, currentStatus?: string) => {
    setVerifyUpdate({ verificationStatus: (currentStatus as any) || 'reviewing' });
    setModal({ type: 'verify', regId });
  };

  const submitStatus = async () => {
    if (!id || !modal) return;
    try {
      await apiEndpoints.auctions.admin.updateRegistrationStatus(id, modal.regId, statusUpdate);
      setModal(null);
      await load();
    } catch (e) {
      setError('Failed to update status');
    }
  };

  const submitDeposit = async () => {
    if (!id || !modal) return;
    try {
      await apiEndpoints.auctions.admin.updateRegistrationDeposit(id, modal.regId, depositUpdate);
      setModal(null);
      await load();
    } catch (e) {
      setError('Failed to update deposit');
    }
  };
  const submitVerify = async () => {
    if (!id || !modal) return;
    try {
      await apiEndpoints.auctions.admin.verifyRegistrationDeposit(id, modal.regId, verifyUpdate);
      setModal(null);
      await load();
    } catch (e) {
      setError('Failed to verify deposit');
    }
  };

  const renderUser = (u: RegItem['userId']) => {
    if (!u || typeof u === 'string') return u || '';
    const name = [u.firstName, u.lastName].filter(Boolean).join(' ');
    return (
      <div className="space-y-0.5">
        <div className="text-sm font-medium">{name || u.username || '(unknown)'}</div>
        <div className="text-xs text-gray-600">{u.email || ''}</div>
      </div>
    );
  };

  return (
    <div className="max-w-7xl mx-auto px-4 py-6">
      <div className="flex items-center justify-between mb-4">
        <button onClick={() => navigate(-1)} className="text-sm text-blue-600"> Back</button>
        <h1 className="text-2xl font-bold">Registrations</h1>
      </div>

      <div className="bg-white rounded-lg shadow p-4 mb-4">
        <div className="flex flex-wrap items-end gap-3">
          <div>
            <label className="block text-xs text-gray-600">Status</label>
            <select value={statusFilter} onChange={(e) => setStatusFilter(e.target.value)} className="px-3 py-2 border rounded-md">
              <option value="">All</option>
              <option value="not_registered">Not Registered</option>
              <option value="registered">Registered</option>
              <option value="deposit_pending">Deposit Pending</option>
              <option value="deposit_received">Deposit Received</option>
              <option value="approved">Approved</option>
              <option value="rejected">Rejected</option>
              <option value="suspended">Suspended</option>
            </select>
          </div>
          <div>
            <label className="block text-xs text-gray-600">Search</label>
            <input value={q} onChange={(e) => setQ(e.target.value)} placeholder="Name or email" className="px-3 py-2 border rounded-md" />
          </div>
          <div>
            <button onClick={load} className="px-4 py-2 bg-black text-white rounded-md">Refresh</button>
          </div>
        </div>
      </div>

      {error && <div className="mb-3 text-red-700 bg-red-50 rounded p-3">{error}</div>}
      {loading ? (
        <div className="text-gray-600">Loading...</div>
      ) : (
        <div className="bg-white rounded-lg shadow overflow-x-auto">
          <table className="min-w-full">
            <thead>
              <tr className="text-left text-xs text-gray-600">
                <th className="p-3">User</th>
                <th className="p-3">Status</th>
                <th className="p-3">KYC</th>
                <th className="p-3">Deposit</th>
                <th className="p-3">Reference</th>
                <th className="p-3">Receipt</th>
                <th className="p-3">Verification</th>
                <th className="p-3">Updated</th>
                <th className="p-3">Actions</th>
              </tr>
            </thead>
            <tbody>
              {items.length === 0 ? (
                <tr><td className="p-3 text-gray-600" colSpan={7}>No registrations</td></tr>
              ) : items.map((r) => (
                <tr key={r._id} className="border-t">
                  <td className="p-3 align-top">{renderUser(r.userId)}</td>
                  <td className="p-3 align-top"><RegistrationBadge status={r.status as any} /></td>
                  <td className="p-3 align-top"><span className="text-xs">{r.kycStatus || 'none'}</span></td>
                  <td className="p-3 align-top">{typeof r.depositAmount === 'number' ? `$${r.depositAmount.toFixed(2)}` : '-'}</td>
                  <td className="p-3 align-top">{r.depositRef || '-'}</td>
                  <td className="p-3 align-top">{r.depositReceiptUrl ? (<a href={r.depositReceiptUrl} target="_blank" rel="noopener noreferrer" className="text-blue-600 underline">View</a>) : '-'}</td>
                  <td className="p-3 align-top"><span className="text-xs">{r.depositVerification?.status || 'none'}</span></td>
                  <td className="p-3 align-top"><span className="text-xs text-gray-600">{r.updatedAt ? new Date(r.updatedAt).toLocaleString() : '-'}</span></td>
                  <td className="p-3 align-top">
                    <div className="flex flex-wrap gap-2">
                      <button className="px-3 py-1 text-white bg-green-600 rounded" onClick={() => { setStatusUpdate({ status: 'approved' }); setModal({ type: 'status', regId: r._id }); }}>Approve</button>
                      <button className="px-3 py-1 text-white bg-orange-600 rounded" onClick={() => openStatusModal(r._id, 'suspended')}>Suspend</button>
                      <button className="px-3 py-1 text-white bg-red-600 rounded" onClick={() => openStatusModal(r._id, 'rejected')}>Reject</button>
                      <button className="px-3 py-1 text-white bg-blue-600 rounded" onClick={() => openDepositModal(r._id, r.depositAmount, r.depositRef)}>Deposit</button>
                      <button className="px-3 py-1 text-white bg-indigo-600 rounded" onClick={() => openVerifyModal(r._id, r.depositVerification?.status)}>Verify</button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      {/* Status Modal */}
      {modal && modal.type === 'status' && (
        <div className="fixed inset-0 bg-black bg-opacity-30 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow p-6 w-full max-w-md">
            <h3 className="text-lg font-semibold mb-3">Update Status</h3>
            <div className="space-y-3">
              <select value={statusUpdate.status} onChange={(e) => setStatusUpdate({ ...statusUpdate, status: e.target.value })} className="w-full px-3 py-2 border rounded-md">
                <option value="approved">Approved</option>
                <option value="registered">Registered</option>
                <option value="deposit_pending">Deposit Pending</option>
                <option value="deposit_received">Deposit Received</option>
                <option value="rejected">Rejected</option>
                <option value="suspended">Suspended</option>
              </select>
              <textarea placeholder="Notes (optional)" className="w-full px-3 py-2 border rounded-md" onChange={(e) => setStatusUpdate({ ...statusUpdate, notes: e.target.value })}></textarea>
              <div className="flex justify-end gap-2">
                <button className="px-4 py-2 bg-black text-white rounded-md" onClick={submitStatus}>Save</button>
                <button className="px-4 py-2 border rounded-md" onClick={() => setModal(null)}>Cancel</button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Deposit Modal */}
      {modal && modal.type === 'deposit' && (
        <div className="fixed inset-0 bg-black bg-opacity-30 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow p-6 w-full max-w-md">
            <h3 className="text-lg font-semibold mb-3">Update Deposit</h3>
            <div className="space-y-3">
              <input type="number" placeholder="Amount" value={depositUpdate.depositAmount ?? ''} onChange={(e) => setDepositUpdate({ ...depositUpdate, depositAmount: parseFloat(e.target.value) })} className="w-full px-3 py-2 border rounded-md" />
              <input type="text" placeholder="Reference" value={depositUpdate.depositRef ?? ''} onChange={(e) => setDepositUpdate({ ...depositUpdate, depositRef: e.target.value })} className="w-full px-3 py-2 border rounded-md" />
              <select value={depositUpdate.status ?? ''} onChange={(e) => setDepositUpdate({ ...depositUpdate, status: e.target.value || undefined })} className="w-full px-3 py-2 border rounded-md">
                <option value="">(no status change)</option>
                <option value="deposit_received">Deposit Received</option>
                <option value="deposit_pending">Deposit Pending</option>
              </select>
              <div className="flex justify-end gap-2">
                <button className="px-4 py-2 bg-black text-white rounded-md" onClick={submitDeposit}>Save</button>
                <button className="px-4 py-2 border rounded-md" onClick={() => setModal(null)}>Cancel</button>
              </div>
            </div>
          </div>
        </div>
      )}

      {modal && modal.type === 'verify' && (
        <div className="fixed inset-0 bg-black bg-opacity-30 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow p-6 w-full max-w-md">
            <h3 className="text-lg font-semibold mb-3">Verify Deposit</h3>
            <div className="space-y-3">
              <select value={verifyUpdate.verificationStatus} onChange={(e) => setVerifyUpdate({ ...verifyUpdate, verificationStatus: e.target.value as any })} className="w-full px-3 py-2 border rounded-md">
                <option value="reviewing">Reviewing</option>
                <option value="verified">Verified</option>
                <option value="rejected">Rejected</option>
              </select>
              <textarea placeholder="Notes (optional)" className="w-full px-3 py-2 border rounded-md" onChange={(e) => setVerifyUpdate({ ...verifyUpdate, notes: e.target.value })}></textarea>
              <label className="flex items-center gap-2"><input type="checkbox" checked={!!verifyUpdate.approve} onChange={(e) => setVerifyUpdate({ ...verifyUpdate, approve: e.target.checked })} /> <span className="text-sm">Approve registration if verified</span></label>
              <div className="flex justify-end gap-2">
                <button className="px-4 py-2 bg-black text-white rounded-md" onClick={submitVerify}>Save</button>
                <button className="px-4 py-2 border rounded-md" onClick={() => setModal(null)}>Cancel</button>
              </div>
            </div>
          </div>
        </div>
      )}

    </div>
  );
}