import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { apiEndpoints } from '../services/api';

type RegStatus = 'not_registered' | 'registered' | 'deposit_pending' | 'deposit_received' | 'approved' | 'rejected' | 'suspended' | null | undefined;

export default function RegisterParticipationPrompt({
  auctionId,
  status,
  auctionStatus,
  onStatusChange
}: {
  auctionId: string;
  status: RegStatus;
  auctionStatus?: 'active' | 'upcoming' | 'ended' | 'cancelled' | string;
  onStatusChange?: (next?: any) => void;
}) {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [loading, setLoading] = React.useState(false);
  const normalized: RegStatus = status || 'not_registered';

  const message = 'Please remember to register your participation in this auction.';

  // Only show for active or upcoming auctions
  const showPrompt = auctionStatus === 'active' || auctionStatus === 'upcoming';
  if (!showPrompt) return null;

  const handleRegister = async () => {
    try {
      setLoading(true);
      const res = await apiEndpoints.auctions.register(auctionId);
      if (onStatusChange) onStatusChange(res.data?.data);
    } finally {
      setLoading(false);
    }
  };

  if (!user) {
    return (
      <div className="flex items-center justify-between gap-2 bg-blue-50 border border-blue-200 text-blue-800 px-3 py-2 rounded-md">
        <div className="text-sm">{message}</div>
        <button className="px-3 py-1 bg-black text-white rounded-md text-sm" onClick={() => navigate('/login')}>Register / Login</button>
      </div>
    );
  }

  if (user && !user.isVerified) {
    return (
      <div className="flex items-center justify-between gap-2 bg-yellow-50 border border-yellow-200 text-yellow-800 px-3 py-2 rounded-md">
        <div className="text-sm">{message}</div>
        <button className="px-3 py-1 bg-black text-white rounded-md text-sm" onClick={() => navigate('/profile')}>Verify Email</button>
      </div>
    );
  }

  if (normalized === 'approved' || normalized === 'deposit_received') {
    return (
      <div className="flex items-center justify-between gap-2 bg-green-50 border border-green-200 text-green-800 px-3 py-2 rounded-md">
        <div className="text-sm">You are registered and cleared to bid on this auction.</div>
      </div>
    );
  }

  return (
    <div className="flex items-center justify-between gap-2 bg-blue-50 border border-blue-200 text-blue-800 px-3 py-2 rounded-md">
      <div className="text-sm">{message}</div>
      {normalized === 'deposit_pending' ? (
        <button className="px-3 py-1 bg-black text-white rounded-md text-sm" onClick={() => navigate(`/auctions/${auctionId}/bank-details`)}>Complete Deposit</button>
      ) : (
        <button className={`px-3 py-1 rounded-md text-sm font-semibold ${loading ? 'bg-gray-200 text-gray-700 cursor-not-allowed' : 'bg-black text-white hover:bg-gray-900'}`} onClick={handleRegister} disabled={loading}>
          {loading ? 'Registering' : 'Register Now'}
        </button>
      )}
    </div>
  );
}