import React from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { apiEndpoints } from '../services/api';
import { useAuth } from '../contexts/AuthContext';

export default function BankDetailsPage() {
  const { id } = useParams();
  const navigate = useNavigate();
  const { user } = useAuth();

  const [loading, setLoading] = React.useState(true);
  const [error, setError] = React.useState<string | null>(null);
  const [bank, setBank] = React.useState<any | null>(null);
  const [amount, setAmount] = React.useState<number | null>(null);
  const [reference, setReference] = React.useState<string | null>(null);
  const [generating, setGenerating] = React.useState(false);

  React.useEffect(() => {
    const load = async () => {
      try {
        setLoading(true);
        setError(null);
        const res = await apiEndpoints.auctions.getBankDetails(String(id));
        const data = res.data?.data || {};
        setBank(data.bank || null);
        setAmount(typeof data.amount === 'number' ? data.amount : null);
      } catch (e: any) {
        setError(e?.response?.data?.message || 'Failed to load bank details');
      } finally {
        setLoading(false);
      }
    };
    if (id) load();
  }, [id]);

  const generateReference = async () => {
    try {
      setGenerating(true);
      const res = await apiEndpoints.auctions.createDepositIntent(String(id));
      const data = res.data?.data || {};
      setReference(data.reference || null);
      if (typeof data.amount === 'number') setAmount(data.amount);
      if (data.bank) setBank(data.bank);
    } catch (e: any) {
      setError(e?.response?.data?.message || 'Failed to generate payment reference');
    } finally {
      setGenerating(false);
    }
  };

  return (
    <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <div className="flex items-center justify-between mb-6">
        <h1 className="text-2xl font-bold">Bank Transfer Details</h1>
        <button className="text-sm text-blue-600" onClick={() => navigate(-1)}>Back</button>
      </div>

      {!user && (
        <div className="mb-4 p-3 rounded bg-yellow-50 border border-yellow-200 text-yellow-800">
          Please log in to generate your payment reference.
          <button className="ml-3 px-3 py-1 bg-black text-white rounded" onClick={() => navigate('/login')}>Login</button>
        </div>
      )}

      {error && (
        <div className="mb-4 p-3 rounded bg-red-50 border border-red-200 text-red-800">{error}</div>
      )}

      {loading ? (
        <div className="p-4 bg-gray-50 border border-gray-200 rounded">Loading bank details</div>
      ) : (
        <div className="bg-white border border-gray-200 rounded-lg p-6">
          <div className="grid grid-cols-1 gap-3 text-sm">
            <div>
              <div className="text-gray-600">Bank institution / name</div>
              <div className="font-semibold">{bank?.bankName || ''}</div>
            </div>
            <div>
              <div className="text-gray-600">Bank account name</div>
              <div className="font-semibold">{bank?.accountName || ''}</div>
            </div>
            <div>
              <div className="text-gray-600">Bank branch name</div>
              <div className="font-semibold">{bank?.branchName || ''}</div>
            </div>
            <div>
              <div className="text-gray-600">Bank branch number</div>
              <div className="font-semibold">{bank?.branchNumber || ''}</div>
            </div>
            <div>
              <div className="text-gray-600">Account number</div>
              <div className="font-semibold">{bank?.accountNumber || ''}</div>
            </div>
            <div>
              <div className="text-gray-600">Suggested deposit amount</div>
              <div className="font-semibold">{typeof amount === 'number' ? `$${amount.toFixed(2)}` : ''}</div>
            </div>
            <div>
              <div className="text-gray-600">Payment reference code</div>
              <div className="font-mono text-base">{reference || ''}</div>
              <div className="mt-2 flex gap-2">
                <button
                  disabled={!user || generating}
                  onClick={generateReference}
                  className={`px-3 py-1 rounded text-sm font-semibold ${!user || generating ? 'bg-gray-200 text-gray-700 cursor-not-allowed' : 'bg-black text-white hover:bg-gray-900'}`}
                >
                  {generating ? 'Generating' : 'Generate Reference'}
                </button>
                <button
                  disabled={!reference}
                  onClick={() => reference && navigator.clipboard && navigator.clipboard.writeText(reference)}
                  className={`px-3 py-1 rounded text-sm ${!reference ? 'bg-gray-200 text-gray-700 cursor-not-allowed' : 'bg-gray-100 text-gray-800 hover:bg-gray-200'}`}
                >
                  Copy Ref
                </button>
              </div>
            </div>
          </div>
          <div className="mt-6 text-xs text-gray-600">
            Use the generated reference exactly as shown when making your bank transfer.
          </div>
        </div>
      )}
    </div>
  );
}