import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';

import api, { apiEndpoints } from '../services/api';

export default function RegisterToBidPanel({
  auctionId,
  status,
  onRegistered,
}: {
  auctionId: string;
  status: string | null | undefined;
  onRegistered?: () => void;
}) {
  const navigate = useNavigate();
  const [depositInfo, setDepositInfo] = React.useState<{ amount?: number; reference?: string } | null>(null);
  const [bankDetails, setBankDetails] = React.useState<{ bankName?: string; accountName?: string; branchName?: string; branchNumber?: string; accountNumber?: string; paymentRefPrefix?: string } | null>(null);
  const { user } = useAuth() as any;
  const [registerLoading, setRegisterLoading] = React.useState(false);

  React.useEffect(() => {
    const loadDeposit = async () => {
      try {
        const { data: res } = await api.get(`/auctions/${auctionId}/deposits/me`);
        const d = res?.data || null;
        if (d) setDepositInfo({ amount: d.depositAmount, reference: d.depositRef });
        // Also refresh registration for verification status
        try {
          const { data: regRes } = await apiEndpoints.auctions.getMyRegistration(auctionId);
          const reg = regRes?.data || null;
          if (reg && reg.depositVerification) {
            setBankDetails((prev) => prev ? prev : null); // keep any loaded bank details
          }
        } catch {}
      } catch {}
    };
    if (status === 'deposit_pending') loadDeposit();
  }, [auctionId, status]);

  const payDeposit = async () => {
    try {
      const { data: res } = await api.post(`/auctions/${auctionId}/deposits/intent`);
      const { paymentUrl, amount, reference, bank } = res?.data || {};
      if (amount || reference) setDepositInfo({ amount, reference });
      if (bank) setBankDetails(bank);
      if (paymentUrl) {
        window.open(paymentUrl, '_blank', 'noopener,noreferrer');
        // Poll registration status for a short period
        let attempts = 0;
        const timer = setInterval(async () => {
          attempts++;
          try {
            const { data: regRes } = await apiEndpoints.auctions.getMyRegistration(auctionId);
            const reg = regRes?.data || null;
            if (reg && (reg.status === 'deposit_received' || reg.status === 'approved')) {
              clearInterval(timer);
              if (onRegistered) onRegistered();
            }
          } catch {}
          if (attempts > 12) clearInterval(timer); // ~1 minute
        }, 5000);
      }
    } catch {}
  };

  const handleLogin = () => navigate('/login');
  const handleRegisterNow = async () => {
    try {
      setRegisterLoading(true);
      const { default: api, apiEndpoints } = await import('../services/api');
      const res = await apiEndpoints.auctions.register(auctionId);
      if (res.data?.success) {
        if (onRegistered) onRegistered();
      }
    } catch {}
    finally {
      setRegisterLoading(false);
    }
  };

  return (
    <div className="bg-gray-50 rounded-lg p-4">
      {status === 'approved' || status === 'deposit_received' ? (
        <div className="text-sm text-green-700">You are approved to bid in this auction.</div>
      ) : status === 'registered' ? (
        <div className="text-sm text-gray-700">Your registration is received. If a deposit is required, please complete it to proceed.</div>
      ) : status === 'deposit_pending' ? (
        <>
          <div className="text-sm text-orange-700 flex items-center justify-between gap-2">
            <div>
              Deposit required. {depositInfo?.amount ? `Amount: $${Number(depositInfo.amount).toFixed(2)}` : ''} {depositInfo?.reference ? `(Ref: ${depositInfo.reference})` : ''}
            </div>
            <button className="px-3 py-1 bg-blue-600 text-white rounded-md hover:bg-blue-700 text-sm" onClick={payDeposit}>Generate Reference</button>
          </div>
          {bankDetails && (
            <div className="mt-3 p-3 bg-white border border-gray-200 rounded-md text-sm text-gray-800">
              <div className="font-semibold mb-1">Bank Transfer Details</div>
              <div>Bank institution / name: {bankDetails.bankName}</div>
              <div>Bank account name: {bankDetails.accountName}</div>
              <div>Bank branch name: {bankDetails.branchName}</div>
              <div>Bank branch number: {bankDetails.branchNumber}</div>
              <div>Account number: {bankDetails.accountNumber}</div>
              <div className="mt-1">Payment ref code: <span className="font-mono">{depositInfo?.reference}</span></div>
              <div className="mt-2 flex gap-2">
                <button
                  className="px-3 py-1 bg-gray-200 rounded-md text-sm"
                  onClick={() => navigator.clipboard && depositInfo?.reference && navigator.clipboard.writeText(depositInfo.reference)}
                >
                  Copy Ref
                </button>
                <label className="px-3 py-1 bg-black text-white rounded-md text-sm cursor-pointer">
                  Upload Proof
                  <input type="file" accept="image/*,application/pdf" className="hidden" onChange={async (e) => {
                    try {
                      const file = e.target.files?.[0];
                      if (!file) return;
                      const reader = new FileReader();
                      reader.onload = async () => {
                        const dataUrl = String(reader.result || '');
                        await apiEndpoints.auctions.uploadDepositReceipt(auctionId, dataUrl);
                        // Optimistically show submitted message
                        setTimeout(async () => {
                          try {
                            const { data: regRes } = await apiEndpoints.auctions.getMyRegistration(auctionId);
                            const reg = regRes?.data || null;
                            // No-op; UI below reads directly from registration status
                          } catch {}
                        }, 500);
                      };
                      reader.readAsDataURL(file);
                    } catch {}
                  }} />
                </label>
              </div>
              <div className="mt-2 text-xs text-gray-600">
                {status === 'deposit_pending' ? 'Proof uploaded. Awaiting verification by admin.' : ''}
              </div>
            </div>
          )}
        </>
      ) : (
        <div className="flex items-center justify-between gap-2">
          <div className="text-sm text-gray-700">You must register to bid in this auction.</div>
          {user ? (
            user.isVerified ? (
              <button className={`px-4 py-2 rounded-md text-sm font-semibold ${registerLoading ? 'bg-gray-200 text-gray-700 cursor-not-allowed' : 'bg-black text-white hover:bg-gray-900'}`} onClick={handleRegisterNow} disabled={registerLoading}>
                {registerLoading ? 'Registering' : 'Register Now'}
              </button>
            ) : (
              <button className="px-4 py-2 bg-black text-white rounded-md hover:bg-gray-900" onClick={() => navigate('/profile')}>Verify Email</button>
            )
          ) : (
            <button className="px-4 py-2 bg-black text-white rounded-md hover:bg-gray-900" onClick={handleLogin}>Register / Login</button>
          )}
        </div>
      )}
    </div>
  );
}