import React, { useEffect, useState } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { useNotification } from '../contexts/NotificationContext';
import { apiEndpoints } from '../services/api';
import { heroThemes, ThemeKey } from '../utils/heroThemes';

interface Cta {
  text: string;
  link: string;
}

interface ImageItem {
  url: string;
  alt?: string;
}

interface HeroSettings {
  title: string;
  subtitle: string;
  theme: ThemeKey;
  images: ImageItem[];
  primaryCta: Cta;
  secondaryCta: Cta;
}

const AdminHeroSettings: React.FC = () => {
  const { user } = useAuth();
  const { showNotification } = useNotification();
  const [loading, setLoading] = useState(false);
  const [settings, setSettings] = useState<HeroSettings>({
    title: 'Welcome to Nexus Auctions',
    subtitle: 'Discover unique items, bid with confidence, and experience the future of online auctions',
    theme: 'default' as ThemeKey,
    images: [],
    primaryCta: { text: 'Browse Auctions', link: '/auctions' },
    secondaryCta: { text: 'Start Selling', link: '/create-auction' }
  });

  useEffect(() => {
    if (!user || (user.role !== 'admin' && user.role !== 'super')) return;
    const load = async () => {
      setLoading(true);
      try {
        const res = await apiEndpoints.settings.hero.get();
        const hero = res.data?.data?.hero || res.data?.hero || res.data || settings;
        setSettings({
          title: hero.title || settings.title,
          subtitle: hero.subtitle || settings.subtitle,
          theme: (hero.theme || 'default') as ThemeKey,
          images: Array.isArray(hero.images) ? hero.images.map((x: any) => ({ url: x.url, alt: x.alt || '' })) : [],
          primaryCta: hero.primaryCta || settings.primaryCta,
          secondaryCta: hero.secondaryCta || settings.secondaryCta
        });
      } catch (e) {
        showNotification('Failed to load hero settings', 'error');
      } finally {
        setLoading(false);
      }
    };
    load();
  }, [user]);

  if (!user || (user.role !== 'admin' && user.role !== 'super')) {
    return (
      <div className="max-w-3xl mx-auto p-6">
        <h1 className="text-2xl font-semibold mb-4">Access Denied</h1>
        <p>You must be an admin to manage hero settings.</p>
      </div>
    );
  }

  const updateField = (field: keyof HeroSettings, value: any) => {
    setSettings(prev => ({ ...prev, [field]: value }));
  };

  const updateImage = (index: number, field: keyof ImageItem, value: string) => {
    setSettings(prev => ({
      ...prev,
      images: prev.images.map((img, i) => i === index ? { ...img, [field]: value } : img)
    }));
  };

  const addImage = () => {
    setSettings(prev => ({ ...prev, images: [...prev.images, { url: '', alt: '' }] }));
  };

  const removeImage = (index: number) => {
    setSettings(prev => ({ ...prev, images: prev.images.filter((_, i) => i !== index) }));
  };

  const save = async () => {
    setLoading(true);
    try {
      const payload = settings;
      const res = await apiEndpoints.settings.hero.update(payload);
      const ok = res.data?.success !== false;
      showNotification(ok ? 'Hero settings updated' : 'Failed to update hero settings', ok ? 'success' : 'error');
    } catch (e) {
      showNotification('Failed to update hero settings', 'error');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-5xl mx-auto p-6">
      <h1 className="text-2xl font-semibold mb-6">Admin: Hero Settings</h1>
      <div className="grid grid-cols-1 gap-6">
        <div className="bg-white rounded-lg shadow p-6">
          <label className="block text-sm font-medium mb-2">Title</label>
          <input className="w-full border rounded px-3 py-2" value={settings.title} onChange={e => updateField('title', e.target.value)} />
        </div>
        <div className="bg-white rounded-lg shadow p-6">
          <label className="block text-sm font-medium mb-2">Subtitle</label>
          <textarea className="w-full border rounded px-3 py-2" rows={3} value={settings.subtitle} onChange={e => updateField('subtitle', e.target.value)} />
        </div>
        <div className="bg-white rounded-lg shadow p-6">
          <label className="block text-sm font-medium mb-2">Theme</label>
          <select className="w-full border rounded px-3 py-2" value={settings.theme} onChange={e => updateField('theme', e.target.value as ThemeKey)}>
            {Object.keys(heroThemes).map(k => (
              <option key={k} value={k}>{k}</option>
            ))}
          </select>
        </div>
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center justify-between mb-2">
            <label className="text-sm font-medium">Images</label>
            <button className="px-3 py-1 bg-blue-600 text-white rounded" onClick={addImage}>Add Image</button>
          </div>
          <div className="space-y-4">
            {settings.images.map((img, i) => (
              <div key={i} className="grid grid-cols-1 md:grid-cols-12 gap-3 items-end">
                <div className="md:col-span-7">
                  <label className="block text-xs mb-1">URL</label>
                  <input className="w-full border rounded px-3 py-2" value={img.url} onChange={e => updateImage(i, 'url', e.target.value)} />
                </div>
                <div className="md:col-span-4">
                  <label className="block text-xs mb-1">Alt</label>
                  <input className="w-full border rounded px-3 py-2" value={img.alt || ''} onChange={e => updateImage(i, 'alt', e.target.value)} />
                </div>
                <div className="md:col-span-1">
                  <button className="w-full px-3 py-2 bg-red-600 text-white rounded" onClick={() => removeImage(i)}>Remove</button>
                </div>
              </div>
            ))}
            {settings.images.length === 0 && (
              <div className="text-sm text-gray-600">No images added.</div>
            )}
          </div>
        </div>
        <div className="bg-white rounded-lg shadow p-6">
          <label className="block text-sm font-medium mb-2">Primary CTA</label>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
            <input className="border rounded px-3 py-2" placeholder="Text" value={settings.primaryCta.text} onChange={e => updateField('primaryCta', { ...settings.primaryCta, text: e.target.value })} />
            <input className="border rounded px-3 py-2" placeholder="Link" value={settings.primaryCta.link} onChange={e => updateField('primaryCta', { ...settings.primaryCta, link: e.target.value })} />
          </div>
        </div>
        <div className="bg-white rounded-lg shadow p-6">
          <label className="block text-sm font-medium mb-2">Secondary CTA</label>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
            <input className="border rounded px-3 py-2" placeholder="Text" value={settings.secondaryCta.text} onChange={e => updateField('secondaryCta', { ...settings.secondaryCta, text: e.target.value })} />
            <input className="border rounded px-3 py-2" placeholder="Link" value={settings.secondaryCta.link} onChange={e => updateField('secondaryCta', { ...settings.secondaryCta, link: e.target.value })} />
          </div>
        </div>
        <div className="flex justify-end">
          <button disabled={loading} onClick={save} className={`px-6 py-2 rounded ${loading ? 'bg-gray-300 text-gray-600' : 'bg-green-600 text-white hover:bg-green-700'}`}>{loading ? 'Saving' : 'Save Settings'}</button>
        </div>
      </div>
    </div>
  );
};

export default AdminHeroSettings;