import React from 'react';

export default function CostBreakdown({
  amount,
  commissionRate = 0.10,
  vatRate = 0.15,
  applyVatOnBid = false,
  stcRate = 0,
}: {
  amount: number;
  commissionRate?: number;
  vatRate?: number;
  applyVatOnBid?: boolean;
  stcRate?: number;
}) {
  const bid = Number(amount) || 0;
  const commission = bid * commissionRate;
  const vatOnCommission = commission * vatRate;
  const vatOnBid = applyVatOnBid ? bid * vatRate : 0;
  const stc = stcRate > 0 ? bid * stcRate : 0;
  const estimatedTotal = bid + commission + vatOnCommission + vatOnBid + stc;

  return (
    <div className="mt-4 p-3 bg-white border rounded-lg">
      <div className="text-sm font-semibold mb-2">Estimated Total</div>
      <div className="space-y-1 text-sm text-gray-800">
        <div className="flex justify-between"><span>Your Bid</span><span>${bid.toFixed(2)}</span></div>
        <div className="flex justify-between"><span>Buyers Commission ({(commissionRate*100).toFixed(0)}%)</span><span>${commission.toFixed(2)}</span></div>
        {applyVatOnBid && (
          <div className="flex justify-between"><span>VAT on Bid ({(vatRate*100).toFixed(0)}%)</span><span>${vatOnBid.toFixed(2)}</span></div>
        )}
        <div className="flex justify-between"><span>VAT on Commission ({(vatRate*100).toFixed(0)}%)</span><span>${vatOnCommission.toFixed(2)}</span></div>
        {stcRate > 0 && (
          <div className="flex justify-between"><span>STC ({(stcRate*100).toFixed(0)}%)</span><span>${stc.toFixed(2)}</span></div>
        )}
        <div className="flex justify-between font-semibold border-t pt-1"><span>Total</span><span>${estimatedTotal.toFixed(2)}</span></div>
      </div>
    </div>
  );
}