import React from 'react';
import { CheckCircle, XCircle, AlertCircle, UserPlus, BadgeCheck, Wallet, ShieldAlert } from 'lucide-react';

type Status = 'not_registered' | 'registered' | 'deposit_pending' | 'deposit_received' | 'approved' | 'rejected' | 'suspended' | null | undefined;

const STATUS_MAP: Record<string, { label: string; cls: string; Icon: React.ElementType }> = {
  not_registered: { label: 'Not Registered', cls: 'bg-gray-100 text-gray-700', Icon: UserPlus },
  registered: { label: 'Registered', cls: 'bg-blue-100 text-blue-700', Icon: BadgeCheck },
  deposit_pending: { label: 'Deposit Pending', cls: 'bg-orange-100 text-orange-700', Icon: AlertCircle },
  deposit_received: { label: 'Deposit Received', cls: 'bg-teal-100 text-teal-700', Icon: Wallet },
  approved: { label: 'Approved', cls: 'bg-green-100 text-green-700', Icon: CheckCircle },
  rejected: { label: 'Rejected', cls: 'bg-red-100 text-red-700', Icon: XCircle },
  suspended: { label: 'Suspended', cls: 'bg-red-100 text-red-700', Icon: ShieldAlert },
};

export default function RegistrationBadge({ status }: { status: Status }) {
  const key = status || 'not_registered';
  const m = STATUS_MAP[key] || STATUS_MAP['not_registered'];
  const Icon = m.Icon;
  return (
    <span className={`px-3 py-1 rounded-full text-sm font-medium inline-flex items-center gap-1 ${m.cls}`}>
      <Icon size={16} /> {m.label}
    </span>
  );
}
