import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';

type StepKey = 'register' | 'verify' | 'update' | 'start';

const steps: { key: StepKey; title: string; description: string; cta: string }[] = [
  { key: 'register', title: 'Register Account', description: 'Create your account with name, email and password.', cta: 'Register Now' },
  { key: 'verify', title: 'Verify Account', description: 'Verify your email address to unlock full access.', cta: 'Verify Now' },
  { key: 'update', title: 'Update Account', description: 'Complete your profile details once-off.', cta: 'Update Now' },
  { key: 'start', title: 'Start Bidding', description: 'Browse auctions and place your first bid.', cta: 'Start Bidding' },
];

const isProfileComplete = (user: any | null) => {
  if (!user) return false;
  const hasPhone = !!user.phone;
  const addr = user.address || {};
  const hasAddress = !!(addr.street || addr.city || addr.state || addr.zipCode || addr.country);
  return hasPhone && hasAddress;
};

export default function OnboardingBanner() {
  const { user, isAuthenticated } = useAuth();
  const navigate = useNavigate();

  const verified = !!(user && (user as any).isVerified);
  const profileComplete = isProfileComplete(user);

  const statusMap: Record<StepKey, { done: boolean; action: () => void }> = {
    register: {
      done: !!isAuthenticated,
      action: () => navigate('/register'),
    },
    verify: {
      done: !!verified || !isAuthenticated,
      action: () => navigate('/profile'),
    },
    update: {
      done: !!profileComplete || !isAuthenticated,
      action: () => navigate('/profile'),
    },
    start: {
      done: false,
      action: () => navigate('/auctions'),
    },
  };

  return (
    <div className="bg-white border-t border-b border-gray-200">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-2xl font-bold text-gray-900">Get Started in 4 Steps</h2>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
          {steps.map((s, idx) => {
            const st = statusMap[s.key];
            const done = st.done;
            return (
              <div key={s.key} className={`rounded-lg p-5 border ${done ? 'border-green-300 bg-green-50' : 'border-gray-200 bg-gray-50'}`}>
                <div className="flex items-center justify-between mb-2">
                  <div className="text-sm font-semibold text-gray-700">{String(idx + 1).padStart(2, '0')}.</div>
                  <span className={`text-xs px-2 py-1 rounded ${done ? 'bg-green-100 text-green-700' : 'bg-gray-100 text-gray-700'}`}>{done ? 'Done' : 'Action Needed'}</span>
                </div>
                <div className="text-lg font-semibold text-gray-900 mb-1">{s.title}</div>
                <div className="text-sm text-gray-600 mb-4">{s.description}</div>
                <button
                  className={`w-full text-sm font-semibold px-4 py-2 rounded ${done ? 'bg-gray-200 text-gray-700 cursor-default' : 'bg-black text-white hover:bg-gray-900'}`}
                  onClick={() => { if (!done) st.action(); }}
                  disabled={done}
                >
                  {done ? 'Completed' : s.cta}
                </button>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}