import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';


import { apiEndpoints } from '../services/api';
import HeroCarousel from '../components/HeroCarousel';
import OnboardingBanner from '../components/OnboardingBanner';
import { heroThemes, ThemeKey } from '../utils/heroThemes';

const Home: React.FC = () => {
  // Removed unused navigate, user, and showNotification to satisfy lint rules.
  const [apiStatus, setApiStatus] = useState<any>(null);
  const [apiError, setApiError] = useState<string | null>(null);
  const [showApiDetails, setShowApiDetails] = useState(false);
  const [isCheckingApi, setIsCheckingApi] = useState(false);
  const [featuredAuctions, setFeaturedAuctions] = useState<any[]>([]);
  const [heroSettings, setHeroSettings] = useState<any>(null);



  useEffect(() => {
    const checkApiStatus = async () => {
      try {
        const response = await apiEndpoints.status();
        setApiStatus(response.data);
        setApiError(null);
      } catch (error: any) {
        console.error('API Status Error:', error);
        const msg = (error?.message || '').toLowerCase();
        const code = error?.code || '';
        const isTimeout = code === 'ECONNABORTED' || msg.includes('timeout');
        setApiError(isTimeout ? 'Unable to reach API (request timed out).' : 'Failed to connect to API');
      }
    };

    checkApiStatus();
  }, []);

  useEffect(() => {
    const loadFeaturedAuctions = async () => {
      try {
        // Try with desired sort first
        let auctions: any[] = [];
        try {
          const res = await apiEndpoints.auctions.getAll({ featured: true, limit: 6, sort: '-views' });
          auctions = res.data?.data?.auctions || res.data?.auctions || res.data || [];
        } catch (primaryErr) {
          console.warn('Primary featured fetch failed, retrying without sort:', primaryErr);
          const res = await apiEndpoints.auctions.getAll({ featured: true, limit: 6 });
          auctions = res.data?.data?.auctions || res.data?.auctions || res.data || [];
        }

        // Fallback: if no featured auctions found, fetch popular active auctions
        if (!Array.isArray(auctions) || auctions.length === 0) {
          try {
            const fallbackRes = await apiEndpoints.auctions.getAll({ status: 'active', limit: 6, sort: '-views' });
            auctions = fallbackRes.data?.data?.auctions || fallbackRes.data?.auctions || fallbackRes.data || [];
          } catch (fallbackErr) {
            console.warn('Fallback active auctions fetch failed:', fallbackErr);
          }
        }

        // Normalize dev-mode variations (images as strings, endDate -> endTime)
        auctions = Array.isArray(auctions) ? auctions.map((a) => {
          const copy = { ...a };
          if (Array.isArray(copy.images) && typeof copy.images[0] === 'string') {
            copy.images = copy.images.map((url: string, index: number) => ({
              url,
              alt: copy.title || 'Auction image',
              isPrimary: index === 0,
              caption: '',
              order: index
            }));
          }
          if (copy.endDate && !copy.endTime) {
            try {
              const d = new Date(copy.endDate);
              copy.endTime = isNaN(d.getTime()) ? copy.endDate : d.toISOString();
            } catch {
              copy.endTime = copy.endDate;
            }
          }
          return copy;
        }) : [];

        setFeaturedAuctions(auctions);
      } catch (error) {
        console.error('Failed to load featured auctions:', error);
      }
    };
    loadFeaturedAuctions();
  }, []);

  useEffect(() => {
    const loadHero = async () => {
      try {
        const res = await apiEndpoints.settings.hero.get();
        const hero = res.data?.data?.hero || res.data?.hero || res.data;
        if (hero) {
          setHeroSettings(hero);
          setSelectedTheme((hero.theme || 'default') as ThemeKey);
        }
      } catch {}
    };
    loadHero();
  }, []);

  const refreshApiStatus = async () => {
    setIsCheckingApi(true);
    try {
      const response = await apiEndpoints.status();
      setApiStatus(response.data);
      setApiError(null);
    } catch (error: any) {
      console.error('API Status Error:', error);
      setApiError(error.message || 'Failed to connect to API');
    } finally {
      setIsCheckingApi(false);
    }
  };

  const [selectedTheme, setSelectedTheme] = useState<ThemeKey>('default');
  const envHero = (process.env.REACT_APP_HERO_IMAGE_URL || '').trim();
  const featuredHeroImages = Array.isArray(featuredAuctions)
    ? featuredAuctions
        .map((a) => {
          const primary = Array.isArray(a.images) ? (a.images.find((img: any) => img.isPrimary) || a.images[0]) : null;
          const url = primary?.url || null;
          return url ? { src: url, alt: a.title || 'Featured auction' } : null;
        })
        .filter(Boolean)
        .slice(0, 5) as { src: string; alt?: string }[]
    : [];

  const heroImages = heroSettings && Array.isArray(heroSettings.images) && heroSettings.images.length > 0
    ? heroSettings.images.map((x: any) => ({ src: x.url, alt: x.alt || '' }))
    : (featuredHeroImages.length > 0
        ? featuredHeroImages
        : (envHero ? [{ src: envHero, alt: 'Nexus Auctions hero' }] : []));
  return (
    <div className="min-h-screen">
      {/* API Status Banner */}
      {apiStatus && (
        <div className="bg-gray-100 border border-gray-400 text-gray-800">
          <div className="px-4 py-3 flex flex-col sm:flex-row sm:items-center sm:justify-between gap-2">
            <div className="text-center sm:text-left">
              <strong>API Connected:</strong> {apiStatus.data?.service} v{apiStatus.data?.version} - Environment: {apiStatus.data?.environment}
            </div>
            <div className="flex items-center justify-center sm:justify-end gap-2 px-2">
              <button
                onClick={() => setShowApiDetails(v => !v)}
                className="bg-gray-200 hover:bg-gray-300 text-black text-sm font-medium px-3 py-1 rounded"
              >
                {showApiDetails ? 'Hide details' : 'View details'}
              </button>
              <a
                href="/api/status"
                target="_blank"
                rel="noopener noreferrer"
                className="bg-white hover:bg-gray-100 text-black border border-gray-400 text-sm font-medium px-3 py-1 rounded"
              >
                Open raw
              </a>
              <button
                onClick={refreshApiStatus}
                disabled={isCheckingApi}
                className={`text-sm font-medium px-3 py-1 rounded border ${isCheckingApi ? 'bg-gray-200 text-gray-600 border-gray-300 cursor-not-allowed' : 'bg-green-200 hover:bg-green-300 text-green-800 border-green-400'}`}
              >
                {isCheckingApi ? 'Refreshing' : 'Refresh'}
              </button>
            </div>
          </div>
          {showApiDetails && (
            <div className="bg-white border-t border-green-200 px-4 py-3 text-left text-sm text-gray-800">
              <div className="flex flex-wrap gap-x-6 gap-y-1">
                <div><span className="font-semibold">Uptime:</span> {typeof apiStatus?.data?.uptime === 'number' ? apiStatus.data.uptime.toFixed(1) : apiStatus?.data?.uptime}s</div>
                <div><span className="font-semibold">Timestamp:</span> {apiStatus?.data?.timestamp ? new Date(apiStatus.data.timestamp).toLocaleString() : ''}</div>
              </div>
              {apiStatus?.data?.endpoints && (
                <div className="mt-2">
                  <div className="font-semibold mb-1">Endpoints:</div>
                  <div className="flex flex-wrap gap-2">
                    {Object.entries(apiStatus.data.endpoints as Record<string, string>).map(([name, path]) => (
                      <a
                        key={name}
                        href={path}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="inline-block bg-gray-100 hover:bg-gray-200 text-gray-800 px-2 py-1 rounded"
                        title={path}
                      >
                        {name}
                      </a>
                    ))}
                  </div>
                </div>
              )}
            </div>
          )}
        </div>
      )}
      {apiError && (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 text-center">
          <strong>API Connection Error:</strong> {apiError}
        </div>
      )}

      {/* Hero Section with Carousel */}
      <section className="relative text-white">
        <HeroCarousel key={selectedTheme} images={heroImages} intervalMs={5000} />
        <div className="absolute inset-0 bg-gradient-to-r from-black/60 to-black/30" />
        <div className="relative py-24 md:py-32">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="text-center">
              <h1 className="text-4xl md:text-6xl font-bold mb-6">
                {heroSettings?.title || 'Welcome to Nexus Auctions'}
              </h1>
              <p className="text-xl md:text-2xl mb-8 max-w-3xl mx-auto">
                {heroSettings?.subtitle || 'Discover unique items, bid with confidence, and experience the future of online auctions'}
              </p>
              {/* Theme selector */}
              <div className="mb-8 flex flex-wrap justify-center gap-2">
                {(
                  [
                    { key: 'default', label: 'Featured' },
                    { key: 'tech', label: 'Tech Gadgets' },
                    { key: 'jewelry', label: 'Jewelry' },
                    { key: 'appliances', label: 'Appliances' },
                    { key: 'cars', label: 'Cars' },
                    { key: 'machinery', label: 'Machinery' },
                  ] as { key: ThemeKey; label: string }[]
                ).map(({ key, label }) => (
                  <button
                    key={key}
                    onClick={() => setSelectedTheme(key)}
                    className={`px-4 py-2 rounded-full text-sm font-semibold border ${
                      selectedTheme === key
                        ? 'bg-white text-primary-700 border-white'
                        : 'bg-white/20 text-white border-white/40 hover:bg-white/30'
                    }`}
                  >
                    {label}
                  </button>
                ))}
              </div>
              <div className="flex flex-col sm:flex-row gap-4 justify-center">
                <Link
                  to={heroSettings?.primaryCta?.link || '/auctions'}
                  className="bg-white text-primary-600 px-8 py-3 rounded-lg font-semibold hover:bg-gray-100 transition-colors duration-200"
                >
                  {heroSettings?.primaryCta?.text || 'Browse Auctions'}
                </Link>
                <Link
                  to={heroSettings?.secondaryCta?.link || '/create-auction'}
                  className="border-2 border-white text-white px-8 py-3 rounded-lg font-semibold hover:bg-white hover:text-primary-600 transition-colors duration-200"
                >
                  {heroSettings?.secondaryCta?.text || 'Start Selling'}
                </Link>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Onboarding Banner */}
      <OnboardingBanner />

      {/* Features Section */}
      <section className="py-16 bg-gray-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-12">
            <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
              Why Choose Nexus Auctions?
            </h2>
            <p className="text-xl text-gray-600 max-w-2xl mx-auto">
              Experience the next generation of online auctions with our cutting-edge platform
            </p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            <div className="text-center p-6">
              <div className="bg-primary-100 w-16 h-16 rounded-full flex items-center justify-center mx-auto mb-4">
                <svg className="w-8 h-8 text-primary-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2zm10-10V7a4 4 0 00-8 0v4h8z" />
                </svg>
              </div>
              <h3 className="text-xl font-semibold text-gray-900 mb-2">Secure Transactions</h3>
              <p className="text-gray-600">
                Advanced security measures ensure your transactions are safe and protected
              </p>
            </div>

            <div className="text-center p-6">
              <div className="bg-primary-100 w-16 h-16 rounded-full flex items-center justify-center mx-auto mb-4">
                <svg className="w-8 h-8 text-primary-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 10V3L4 14h7v7l9-11h-7z" />
                </svg>
              </div>
              <h3 className="text-xl font-semibold text-gray-900 mb-2">Real-time Bidding</h3>
              <p className="text-gray-600">
                Experience lightning-fast bidding with real-time updates and notifications
              </p>
            </div>

            <div className="text-center p-6">
              <div className="bg-primary-100 w-16 h-16 rounded-full flex items-center justify-center mx-auto mb-4">
                <svg className="w-8 h-8 text-primary-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
              </div>
              <h3 className="text-xl font-semibold text-gray-900 mb-2">Verified Items</h3>
              <p className="text-gray-600">
                All items are thoroughly verified to ensure authenticity and quality
              </p>
            </div>
          </div>
        </div>
      </section>

      {/* Featured Auctions */}
      <section className="py-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-12">
            <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
              Featured Auctions
            </h2>
            <p className="text-xl text-gray-600">
              Don't miss out on these exciting auction opportunities
            </p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {featuredAuctions.length === 0 && (
              <div className="col-span-full text-center text-gray-600">
                No featured auctions available right now.
              </div>
            )}
            {featuredAuctions.map((a) => (
              <div key={a._id} className="bg-white rounded-lg shadow-lg overflow-hidden hover:shadow-xl transition-shadow duration-200">
                <div className="h-48 bg-gray-200">
                  {Array.isArray(a.images) && a.images.length > 0 ? (
                    <img
                      src={(a.images.find((img: any) => img.isPrimary) || a.images[0])?.url}
                      alt={(a.images.find((img: any) => img.isPrimary) || a.images[0])?.alt || a.title}
                      className="h-48 w-full object-cover"
                    />
                  ) : (
                    <div className="h-full w-full flex items-center justify-center">
                      <span className="text-gray-500">No image</span>
                    </div>
                  )}
                </div>
                <div className="p-6">
                  <h3 className="text-xl font-semibold text-gray-900 mb-2">
                    {a.title}
                  </h3>
                  <p className="text-gray-600 mb-4 line-clamp-3">
                    {a.description}
                  </p>
                  <div className="flex justify-between items-center mb-4">
                    <span className="text-sm text-gray-500">Current Bid</span>
                    <span className="text-lg font-bold text-primary-600">
                      ${typeof a.currentBid === 'number' ? a.currentBid.toLocaleString() : (a.currentBid || 0)}
                    </span>
                  </div>
                  <div className="flex justify-between items-center mb-4">
                    <span className="text-sm text-gray-500">Ends</span>
                    <span className="text-sm font-medium text-secondary-600">
                      {a.endTime ? new Date(a.endTime).toLocaleString() : 'TBD'}
                    </span>
                  </div>
                  <Link
                    to={`/auctions/${a._id}`}
                    className="w-full inline-block text-center bg-primary-600 text-white px-4 py-2 rounded-lg font-semibold hover:bg-primary-700 transition-colors duration-200"
                  >
                    View Details
                  </Link>
                </div>
              </div>
            ))}
          </div>

          <div className="text-center mt-12">
            <Link
              to="/auctions"
              className="bg-primary-600 text-white px-8 py-3 rounded-lg font-semibold hover:bg-primary-700 transition-colors duration-200"
            >
              View All Auctions
            </Link>
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="bg-primary-600 text-white py-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <h2 className="text-3xl md:text-4xl font-bold mb-4">
            Ready to Start Bidding?
          </h2>
          <p className="text-xl mb-8 max-w-2xl mx-auto">
            Join thousands of satisfied users who trust Nexus Auctions for their buying and selling needs
          </p>
          <button className="bg-white text-primary-600 px-8 py-3 rounded-lg font-semibold hover:bg-gray-100 transition-colors duration-200">
            Get Started Today
          </button>
        </div>
      </section>
    </div>
  );
};

export default Home;