export function resolveImageUrl(url: string | undefined | null): string {
  if (!url) return '/api/placeholder/800/600?text=No+Image';
  const trimmed = url.trim();

  // Data/blob URLs are fine
  if (trimmed.startsWith('data:image/') || trimmed.startsWith('blob:')) {
    return trimmed;
  }

  // Allow public assets served by CRA without proxying
  const publicPrefixes = ['/assets/', '/static/', '/favicon', '/logo', '/manifest', '/robots.txt'];
  if (publicPrefixes.some((p) => trimmed.startsWith(p))) {
    return trimmed;
  }

  // Already proxied or our dev placeholder
  if (trimmed.startsWith('/api/placeholder') || trimmed.startsWith('/api')) {
    return trimmed;
  }

  const isAbsolute = /^https?:\/\//i.test(trimmed);
  if (isAbsolute) {
    try {
      const u = new URL(trimmed);
      const host = u.hostname.toLowerCase();
      const pathname = u.pathname.toLowerCase();

      // Common image extensions
      const hasImageExt = /\.(jpg|jpeg|png|gif|webp|svg)(\?.*)?$/.test(pathname);

      // Known hosts that serve images without file extensions
      const knownImageHosts = new Set([
        'images.unsplash.com',
        'via.placeholder.com',
        'i.imgur.com',
        'res.cloudinary.com'
      ]);

      if (hasImageExt || knownImageHosts.has(host)) {
        return trimmed;
      }

      // Route via backend image proxy to validate content-type and avoid ORB/CORS issues
      const proxied = `/api/placeholder/proxy?url=${encodeURIComponent(trimmed)}`;
      return proxied;
    } catch {
      // Malformed URL, fall back
      return '/api/placeholder/800/600?text=No+Image';
    }
  }

  // Relative paths: proxy through /api except for public prefixes handled above
  const withSlash = trimmed.startsWith('/') ? trimmed : `/${trimmed}`;
  return `/api${withSlash}`;
}

export function getPrimaryImageUrl(images?: Array<{ url: string; isPrimary?: boolean }>): string {
  if (!images || images.length === 0) return '';
  const primary = images.find((i) => i.isPrimary) || images[0];
  return resolveImageUrl(primary?.url);
}